<template>
  <div class="charts-container">
    <div class="chart-row">
      <div ref="pieChart" class="chart-item"></div>
      <div ref="categoryChart" class="chart-item"></div>
    </div>
    <div class="chart-row">
      <div ref="timelineChart" class="chart-item wide"></div>
    </div>
  </div>
</template>
 <script>
 import * as echarts from 'echarts';
 export default {
  name: 'TaskChart',
  props: {
    tasks: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      pieChart: null,
      categoryChart: null,
      timelineChart: null
    }
  },
  mounted() {
    this.initCharts();
    window.addEventListener('resize', this.resizeCharts);
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.resizeCharts);
    if (this.pieChart) this.pieChart.dispose();
    if (this.categoryChart) this.categoryChart.dispose();
    if (this.timelineChart) this.timelineChart.dispose();
  },
  methods: {
    initCharts() {
      this.initPieChart();
      this.initCategoryChart();
      this.initTimelineChart();
    },
    
    // 完成状态饼图
    initPieChart() {
      this.pieChart = echarts.init(this.$refs.pieChart);
      const option = {
        title: {
          text: '完成状态',
          left: 'right',
          fontSize: 14
        },
        tooltip: {
          trigger: 'item',
          formatter: '{b}: {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          left: 'left',
          textStyle: {
       fontSize: 12
     }
        },
        series: [
          {
            type: 'pie',
            radius: ['30%', '50%'],
            avoidLabelOverlap: false,
            itemStyle: {
              borderRadius: 6,
              borderColor: '#fff',
              borderWidth: 1
            },
            label: {
              show: true,
              formatter: '{b}: {c}\n{d}%',
              fontSize: 12,
              alignTo: 'edge', // 将标签对齐到边缘
         edgeDistance: '10%', // 距离边缘的距离
         position: 'outside', // 标签位置在外部
         distanceToLabelLine: 5 // 标签线的长度
            },
            labelLine: {
         length: 15, // 第一段线长度
         length2: 10, // 第二段线长度
         smooth: true // 使用平滑曲线
       },  
       labelLayout: {
         hideOverlap: true // 防止标签重叠
       },
            emphasis: {
              label: {
                show: true,
                fontSize: 16,
                fontWeight: 'bold'
              }
            },
            data: [
              {
                value: this.tasks.filter(t => t.done).length,
                name: '已完成',
                itemStyle: { color: '#91cc75' }
              },
              {
                value: this.tasks.filter(t => !t.done).length,
                name: '进行中',
                itemStyle: { color: '#fac858' }
              }
            ]
          }
        ]
      };
      this.pieChart.setOption(option);
    },
     // 分类统计图
    initCategoryChart() {
      this.categoryChart = echarts.init(this.$refs.categoryChart);
      const categories = {};
      this.tasks.forEach(task => {
        categories[task.category] = (categories[task.category] || 0) + 1;
      });
       const option = {
        title: {
          text: '任务分类统计',
          left: 'center',
         
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        xAxis: {
          type: 'category',
          data: Object.keys(categories),
          axisLabel: {
            interval: 0,
            rotate: 30
          }
        },
        yAxis: {
          type: 'value',
          name: '任务数量',
          minInterval: 1,    // 设置最小间隔为1
     splitNumber: 5,    // 设置分割段数
     axisLabel: {
       formatter: '{value}'  // 使用整数格式
     }
        },
        series: [
          {
            data: Object.values(categories),
            type: 'bar',
            showBackground: true,
            backgroundStyle: {
              color: 'rgba(180, 180, 180, 0.2)'
            },
            itemStyle: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                { offset: 0, color: '#83bff6' },
                { offset: 0.5, color: '#188df0' },
                { offset: 1, color: '#188df0' }
              ])
            }
          }
        ]
      };
      this.categoryChart.setOption(option);
    },
     // 时间趋势图
    initTimelineChart() {
      this.timelineChart = echarts.init(this.$refs.timelineChart);
      
      // 获取最近7天的日期
      const dates = Array.from({length: 7}, (_, i) => {
        const d = new Date();
        d.setDate(d.getDate() - i);
        return d.toISOString().split('T')[0];
      }).reverse();
       // 统计每天的完成任务数
      const completedTasks = dates.map(date => {
        return this.tasks.filter(task => {
          const taskDate = new Date(task.createdAt).toISOString().split('T')[0];
          return taskDate === date && task.done;
        }).length;
      });
       // 统计每天的新增任务数
      const newTasks = dates.map(date => {
        return this.tasks.filter(task => {
          const taskDate = new Date(task.createdAt).toISOString().split('T')[0];
          return taskDate === date;
        }).length;
      });
       const option = {
        title: {
          text: '任务趋势分析',
          left: 'center'
        },
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: ['现有任务', '完成任务'],
          top: 30
        },
        xAxis: {
          type: 'category',
          data: dates,
          axisLabel: {
            formatter: (value) => value.slice(5) // 只显示月-日
          }
        },
        yAxis: {
          type: 'value',
          name: '任务数量'
        },
        series: [
          {
            name: '现有任务',
            type: 'line',
            smooth: true,
            data: newTasks,
            itemStyle: {
              color: '#5470c6'
            }
          },
          {
            name: '完成任务',
            type: 'line',
            smooth: true,
            data: completedTasks,
            itemStyle: {
              color: '#91cc75'
            }
          }
        ]
      };
      this.timelineChart.setOption(option);
    },
     resizeCharts() {
      if (this.pieChart) this.pieChart.resize();
      if (this.categoryChart) this.categoryChart.resize();
      if (this.timelineChart) this.timelineChart.resize();
    }
  },
  watch: {
    tasks: {
      handler() {
        this.initCharts();
      },
      deep: true
    }
  }
 };
 </script>
 <style scoped>
 charts-container {
  display: flex;
  flex-direction: row;
  gap: 15px;
  padding: 10px;
 }
 .chart-row {
  display: flex;
  gap: 20px;
 }
 .chart-item {
  flex: 1;
  height: 300px;
  border: 1px solid #eee;
  border-radius: 6px;
  padding: 15px;
  background: linear-gradient(to bottom right, #ffffff, #f8f9fa);
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.1);
 }
 .chart-item.wide {
  width: 100%;
 }
 @media (max-width: 768px) {
  .chart-row {
    flex-direction: column;
  }
  
  .chart-item {
    height: 250px;
  }
 }
 </style>